.TodoItem {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    align-items: center;
    background-color: #0288D1;
    color: #fff;
    min-height: 50px;
    margin-bottom: 5px;
    border-radius: 5px;
    padding: 10px 20px;
    overflow-wrap: break-word;
}

.TodoItem__value {
    width: 100%;
    min-width: 0;
    cursor: pointer;

}

.TodoItem__icon {
    width: 15px;
    height: 15px;
}

.TodoItem__controls {
    display: flex;
    flex-shrink: 0;
    gap: 5px;
}

.TodoItem__input {
    display: block;
    border: none;
    outline: none;
    border-radius: 5px;
    width: 100%;
}

.TodoItem__input--edit {
    border-radius: 0px;
    width: 100%;
}

.TodoItem__button {
    border: none;
    outline: none;
    cursor: pointer;
}

.TodoItem__button--edit {
    background-color: #3cbeff;
}

.TodoItem__button--edit:hover {
    background-color: #82d5ff;
}

.TodoItem__button--important {
    background-color: #FFEB3B;
}

.TodoItem__button--important:hover {
    background-color: #FFFF00;
}

.TodoItem__button--trash {
    background-color: #D84315;
}

.TodoItem__button--trash:hover {
    background-color: #FF7043;
}

.TodoItem--done .TodoItem__value {
    text-decoration: line-through;
}

.TodoItem--important  {
    background-color: #ff9650;
}


@media (max-width: 400px) {
    .TodoItem__value  {
        font-size: 16px;
    }

    .TodoItem__input {
        font-size: 16px;
        min-height: 100%;
        height: 100%;
    }
}
